:root {
  --font-second: Arial, Helvetica, sans-serif;
  --font-size: 16px;
  --color-bg: #ffffff;
  --color-white: #ffffff;
  --color-dark: #484848;
  --color-green: #12b992;
  --color-orange: #f8c227;
  --color-blue: #225980;
  --color-gray: #413e5e;
}
a {
  text-decoration: none !important;
  color: inherit;
}

html {
  background-color: var(--color-bg);
  margin-right: 0 !important;
}
body {
  font-family: var(--font-main);
  font-style: normal;
  font-weight: normal;
  font-size: var(--font-size-var2);
  color: var(--color-dark);
  margin: 0;
  padding: 0;
}
.wrapper {
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  min-height: 100%;
}
img {
  max-width: 100%;
}

sup {
  font-size: 0.6em;
}
sub {
  font-size: 0.6em;
}
ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.bg {
  position: relative;
  background: #ffffff url(../../public/bg.svg);
  height: 100vh;
  background-size: cover;
  background-position: 0 0;
  background-repeat: repeat-x;
  animation: animate 5s linear infinite;
}
.car {
  position: absolute;
  bottom: 100px;
  left: 90px;
  animation: car 1s linear infinite;
}
.sky {
  position: absolute;
  top: 5%;
  left: 43%;
  animation: baloon 60s infinite both;
  animation: baloon_2 2s 2.2s infinite both;
  width: 200px;
}
@keyframes animate {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 100% 0;
  }
}
@keyframes car {
  0% {
    transform: translateY(-1px);
  }
  50% {
    transform: translateY(1px);
  }
  100% {
    transform: translateY(-1px);
  }
}
@keyframes baloon {
  0% {
    transform: translateX(0px);
  }
  50% {
    transform: translateX(200px);
  }
  100% {
    transform: translateX(0px);
  }
}
@keyframes baloon_2 {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(20px);
  }
  100% {
    transform: translateY(0);
  }
}
@media (max-width: 768px) {
  .bg {
    position: relative;
    background: #ffffff url(../../public/bg.svg);
    height: 100vh;
    background-size: cover;
    background-position: 0 0;
    background-repeat: repeat-x;
    animation: animate 12s linear infinite;
  }
  .car {
    max-width: 60%;
    position: absolute;
    bottom: 60px;
    left: 60px;
  }
  .sky {
    position: absolute;
    top: 5%;
    left: 33%;
    animation: baloon 60s infinite both;
    animation: baloon_2 2s 2.2s infinite both;
    width: 100px;
  }
}
